/* 기본 레이아웃 설정 */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

/* hero 섹션 스타일 */
.hero-section {
  width: 100%;
  overflow: hidden;
}

.hero-slider {
  display: flex;
}

.hero-slide {
  flex: 1;
  position: relative;
}

.hero-slide img {
  width: 100%;
  height: auto;
}

.hero-text {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
}

/* 인기 여행지 섹션 스타일 */
.region-section {
  padding: 20px;
  text-align: center;
}

.region-carousel {
  max-width: 1200px;
  margin: 0 auto; /* 중앙 정렬 */
  padding: 0 20px; /* 양 옆 여백 추가 */
}

.region-carousel .carousel-item {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  margin: auto;
  border: none;
}

.region-carousel .carousel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.region-carousel .carousel-text {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-weight: bold;
}

/* 상품 섹션 스타일 */
.product-section {
  padding: 20px;
  background-color: #f9f9f9;
}

.product-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto; /* 중앙 정렬 */
  padding: 0 20px; /* 양 옆 여백 추가 */
}

.product-card {
  flex: 1 1 calc(25% - 20px);
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.product-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.product-info {
  padding: 10px;
}

.product-badge {
  background-color: #007bff;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  display: inline-block;
  margin-bottom: 10px;
}

.product-tags {
  color: #555;
  margin-bottom: 10px;
}

.product-title {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.product-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.coupon {
  background-color: #ff9800;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  margin-right: 10px;
}

.coupon-value {
  font-weight: bold;
  color: #ff9800;
}

.price {
  font-size: 1.2em;
  font-weight: bold;
  color: #e91e63;
}

.product-card:hover {
  transform: scale(1.05);
}

