.navigation-bar {
  background-color: white;
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}

.top-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 20px;
}

.left-nav {
  display: flex;
  align-items: center;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  margin-right: 20px;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-right {
  display: flex;
  gap: 20px;
  padding-right: 20px; /* 오른쪽 요소들을 왼쪽으로 이동시키기 위한 패딩 추가 */
}

.categories {
  display: flex;
  justify-content: center;
  gap: 20px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
  margin-top: 10px;
}

.nav-links li, .nav-right li {
  list-style: none;
}

.nav-links a, .nav-right a, .categories a {
  text-decoration: none;
  color: black;
  padding: 5px 10px;
}

.nav-links a:hover, .nav-right a:hover, .categories a:hover {
  background-color: #f0f0f0;
}

/* 미디어 쿼리 추가 */
@media (max-width: 1024px) {
  .top-nav {
      flex-direction: column;
      align-items: flex-start;
  }

  .nav-links, .nav-right {
      flex-direction: column;
      width: 100%;
      gap: 10px;
  }

  .nav-links a, .nav-right a {
      width: 100%;
      text-align: left;
  }

  .categories {
      flex-direction: column;
      align-items: flex-start;
  }
}
