.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.detail-page {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
}

.detail-content {
  width: 70%;
}

.detail-sidebar {
  width: 25%;
}

.sticky-sidebar {
  position: sticky;
  top: 20px;
}

.price-info-box {
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.price-info {
  text-align: center;
  margin-bottom: 20px;
}

.original-price {
  font-size: 14px;
  text-decoration: line-through;
  margin-right: 10px;
}

.discount {
  font-size: 14px;
  color: red;
}

.discounted-price {
  font-size: 32px;
  font-weight: bold;
}

.per-person {
  font-size: 14px;
}

.booking-info {
  text-align: center;
}

.book-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  margin-bottom: 10px;
  cursor: pointer;
}

.book-button:hover {
  background-color: #0056b3;
}

.wishlist-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #f8f9fa;
  color: black;
  border: 1px solid #ced4da;
  margin-bottom: 10px;
  cursor: pointer;
}

.wishlist-button:hover {
  background-color: #e2e6ea;
}

.wishlist-count {
  font-size: 12px;
  color: #6c757d;
}

.provider-info {
  text-align: center;
  margin-top: 20px;
}

.inquiry-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  cursor: pointer;
}

.inquiry-button:hover {
  background-color: #218838;
}

.event-info-box {
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.event-info-box h2 {
  margin-top: 0;
  color: #ff5733; /* 특별 할인 제목 색상 */
}

.event-info-box ul {
  padding-left: 20px;
  list-style-type: none; /* 기본 스타일 제거 */
}

.event-info-box ul li {
  list-style: none; /* 기본 스타일 제거 */
}

.option-section {
  margin-top: 20px;
}

.option-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.date-picker-container,
.num-people-container {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #f8f9fa;
  border: 1px solid #ced4da;
  border-radius: 5px;
  flex: 1;
  position: relative;
}

.date-picker-container .custom-datepicker,
.num-people-container .custom-input {
  border: none;
  background: none;
  margin-left: 10px;
  flex: 1;
}

.custom-datepicker:focus,
.custom-input:focus {
  outline: none;
}

.num-people-container.disabled {
  opacity: 0.5;
}

.price-check-button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  flex: 1;
  text-align: center;
}

.price-check-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.price-query-info {
  margin-top: 10px;
  text-align: right;
}

.price-query-info a {
  color: #007bff;
  text-decoration: none;
}

.dropdown-menu {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  background: white;
  border: 1px solid #ced4da;
  border-radius: 5px;
  max-height: 200px;
  overflow-y: auto;
  z-index: 1000;
}

.dropdown-menu li {
  padding: 10px;
  cursor: pointer;
  list-style: none;
}

.dropdown-menu li:hover {
  background-color: #f1f1f1;
}
