.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: orange;
  color: white;
  padding: 50px 20px;
  position: relative;
}

.footer-content {
  width: 50%;
  padding: 20px;
}

.footer-content h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.footer-content p {
  font-size: 18px;
  margin-bottom: 10px;
}

.footer-image {
  width: 50%;
  overflow: hidden;
}

.footer-image img {
  width: 100%;
  height: auto;
}

.footer-bottom {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: black; 
}
